ng that the customer is here ready to go through finance, the deal folder is signed, and the information in that deal folder is complete with all dates valid. Again if you are click the finance button before the make ready button the finance button will become disabled after clicking. If you have already sent the request to make ready and then click the finance button the request will drop off the sales manager view.")]),_vm._v(" "),_c('div',{staticClass:"title"},[_vm._v("Approving We Owe Requests")]),_vm._v(" "),_c('div',{staticClass:"body-1"},[_vm._v("When a sales associate submits a we owe request it will come to you for approval. The request will populate under the \"We Owes\" label. All of the information will be presented to you in a table format. Along with the information about the request you will be presented with two buttons, one green button that says \"Approve\" and one red button that says \"Deny\". By clicking either one of the buttons you are doing to perform that action. If you click deny you will stop the request from continuing in the process. If you approve the request the information will be passed to the parts department for them to start working.")])],1)],1)],1)}
